// 拦截器
import axios from 'axios'
import cmsMessage from '@/components/cms/cmsMessage'
import router from './../router/index'

/**
 * 配置全局参数请求头
 */
const $axios = axios.create({
  baseURL: process.env.baseUrl,
  timeout: 50000,
  headers: {
    'JSPGOU-Auth-Token': localStorage.getItem('platKey'),
    'Redirect-Header': false
  }
})
/**
 * 请求拦截器
 */
$axios.interceptors.request.use(config => { // 在此处统一配置公共参数
  config.headers['JSPGOU-Auth-Token'] = localStorage.getItem('platKey')
  config.headers['Redirect-Header'] = false
  return config
}, error => {
  Promise.reject(error) // 错误提示
})

/** 响应拦截器 */
$axios.interceptors.response.use(response => {
  const code = response.data.code
  if (response.data.token && response.data.token !== '') {
    localStorage.setItem('platKey', response.data.token)
  }
  if (code < 10000 && code !== 200 && code !== 100 && code !== 504) {
    if (code === 501 || code === 502 || code === 503 || code === 506) {
      const $zoneEl = document.querySelector('#zoneDialog')
      if ($zoneEl) {
        document.body.removeChild($zoneEl)
      }
      localStorage.setItem('platKey', '')
      router.push('/login')
    }
    cmsMessage({
      message: response.data.message,
      type: 'error',
      timer: '2500'
    })
  }
  return response.data
},
error => {
  cmsMessage({
    message: '服务端加载失败',
    type: 'error',
    timer: '1500'
  })
  // if (process.env.NODE_ENV === '') {
  //   location.href = '/static/error/error.html'
  // } else {
  //   location.href = '../static/error/error.html'
  // }

  return Promise.reject(error)
}
)
export default $axios



