<template>
  <div class="cms-upload" @click="selectFile">
    <slot></slot>
    <input type="file" name='fileName' multiple="multiple" ref='uploadFile' class="cms-upload-input" @change="onUpLoad($event)" v-if="clearShow">
  </div>
</template>
<script>
function noop() {}
// 需求不明确,确定后再完善
export default {
  name: 'cmsUpload',
  props: {
    onChange: {
      // 返回选择的文件列表
      type: Function,
      default: noop
    },
    type: {
      type: String,
      default: 'all'
    }
  },
  data() {
    return {
      clearShow: true,
      typeList: {
        image: [
          'bmp',
          'jpg',
          'png',
          'tif',
          'gif',
          'pcx',
          'tga',
          'exif',
          'fpx',
          'svg',
          'psd',
          'cdr',
          'pcd',
          'dxf',
          'ufo',
          'eps',
          'ai',
          'raw',
          'WMF',
          'webp'
        ],
        video: [
          'mp4',
          'flv',
          'f4v',
          'webm',
          'm4v',
          'mov',
          '3gp',
          '3g2',
          'wmv',
          'avi',
          'asf',
          'rm',
          'rmvb'
        ],
        audio: ['mp3', 'aac', 'ac3', 'wav', 'm4a', 'ogg'],
        file: []
      }
    }
  },
  methods: {
    validateType(files) {
      const self = this
      return new Promise((resolve, reject) => {
        if (this.type === 'all') {
          resolve({
            status: true,
            type: 'all'
          })
        } else {
          let status = false
          let type = ''
          for (let i = 0; i < files.length; i++) {
            const fileName = files[i].name
            // 返回String对象中子字符串最后出现的位置.
            const seat = fileName.lastIndexOf('.') + 1
            // 返回位于String对象中指定位置的子字符串并转换为小写.
            const extension = fileName.substring(seat).toLowerCase()
            const allowed = self.typeList[self.type]
            if (allowed.indexOf(extension) > -1) {
              status = true
            } else {
              status = false
            }
            type = extension
          }

          resolve({
            status: status,
            type: type
          })
        }
      })
    },
    selectFile() {
      // 选择上传的文件
      this.$refs['uploadFile'].click()
    },
    onUpLoad(el) {
      // 返回上传的文件
      const files = el.target.files
      this.validateType(files).then(res => {
        if (res.status) {
          this.onChange(el.target.files)
        } else {
          this.$errorMessage('不支持.' + res.type + '格式文件')
        }
      })
      this.clearShow = false
      setTimeout(() => {
        this.clearShow = true
      }, 5)
    }
  }
}
</script>

<style scoped lang='scss'>
.cms-upload {
  display: inline-block;
}
.cms-upload-input {
  display: none;
}
</style>



