import { userlogin, userlogOut } from '@/api/login/login'
import cms from '@/utils/index'

// 登录是通用的根据不同的值来区分
const user = {
  state: {
    token: '',
    userName: '',
    lastLoginTime: '',
    lastLoginIP: '',
    userId: ''
  },
  mutations: {
    SET_USER_INfO: (state, obj) => {
      state.userName = obj.userName
      state.lastLoginTime = obj.lastLoginTime
      state.lastLoginIP = obj.lastLoginIP
      state.userId = obj.userId
    },
    SET_LOGIN_INfO: (state, obj) => {
      state.token = obj.token
    },
    CLEAR_LOGIN_INFO: (state) => {
      state.token = ''
      state.userName = ''
    }
  },
  actions: {
    setUserInfo({ commit }, obj) {
      commit('SET_USER_INfO', { ...obj })
    },
    login({ commit }, formData) { // 在这里调用加密
      return new Promise((resolve, reject) => {
        const params = {
          identity: formData.userName,
          desStr: cms.encrypt(JSON.stringify({ pStr: formData.password })),
          captcha: formData.captcha,
          sessionId: formData.sessionId
        }
        userlogin(params).then(res => { // 登录返回的信息，会包含菜单和权限
          resolve(res)
          if (res.code === 200) {
            localStorage.setItem('platKey', res.data['JSPGOU-Auth-Token'])
            commit('SET_LOGIN_INfO', {
              token: res.data['JSPGOU-Auth-Token']
            })
            const { userName, lastLoginTime, lastLoginIP, userId } = res.data.auth
            commit('SET_USER_INfO', {
              userName,
              lastLoginTime,
              lastLoginIP,
              userId
            })
          }
        }).catch(e => {
          reject(e)
        })
      })
    },
    logOut({ commit }, formData) { // 退出登录
      return new Promise((resolve, reject) => {
        userlogOut(formData).then(res => {
          if (res.code === 200) {
            commit('CLEAR_LOGIN_INFO')
            resolve()
          }
        })
      })
    }
  }
}
export default user



