<template>
  <el-date-picker
      v-model="checkedTimer"
      :type="type"
      :picker-options="disabledOption"
      range-separator="至"
      start-placeholder="开始日期"
      end-placeholder="结束日期"
      align="right"
      style="width:285px"
      :default-time="['00:00:00', '23:59:59']"
      :value-format="valueFormat"
      @change="getTime"
      >
    </el-date-picker>
</template>

<script>
export default {
  name: 'cmsPicker',
  props: {
    value: {
      type: [String, Date, Array]
    },
    type: {
      type: String,
      default: 'daterange'// 显示类型
    },
    disabledType: {
      type: String,
      default: ''// 当前时间日期选择器特有的选项参考下表
    },
    valueFormat: {
      type: String,
      default: 'yyyy-MM-dd'// 显示在输入框中的格式
    }

  },
  data() {
    return {
      checkedTimer: this.value,
      disabledOption: {},
      pickerOptions: {// 选择今天以前的时间
        disabledDate(time) {
          return time.getTime() > Date.now()
        }
      },
      pickerOptions2: {// 选择今天（包括今天在内）以后的时间
        disabledDate(time) {
          return time.getTime() < (Date.now() - 86400000)
        }
      },
      pickerOptions3: {// 选择今天（不包括今天在内）以后的时间
        disabledDate(time) {
          return time.getTime() < (Date.now())
        }
      }
    }
  },
  watch: {
    value() {
      this.checkedTimer = this.value
    },
    disabledType: {
      handler() {
        this.setDisableType(this.disabledType)
      },
      immediate: true
    }
  },
  methods: {
    setFilterTime(time) { // 传入要限制的时间
      if (time) {
        time = time.replace(/-/g, '/')
        const date = new Date(time)
        const options = {
          disabledDate(time) {
            return time.getTime() < (date.getTime() + 86400000)
          }
        }
        this.setDisableType('custom', options)
      }
    },
    setDisableType(type, disabledDate) {
      if (type === 'after') { // 今天以前
        this.disabledOption = Object.assign({}, this.pickerOptions)
      }
      if (type === 'before') { // 今天以后（包括今天）
        this.disabledOption = Object.assign({}, this.pickerOptions2)
      }
      if (type === 'todayAfter') { // 今天以后（不包括今天）
        this.disabledOption = Object.assign({}, this.pickerOptions3)
      }
      if (type === '') {
        this.disabledOption = {}
      }
      if (type === 'custom') {
        this.disabledOption = Object.assign({}, disabledDate)
      }
    },
    getTime(value) {
      if (value === null) {
        this.$emit('change', [])
        this.$emit('input', [])
      } else {
        this.$emit('change', value)
        this.$emit('input', value)
      }
    }
  }
}
</script>

<style>

</style>



