<template>
 <div  class="cms-pagination">
  <el-pagination
    :style='{float:align}'
    background
    :page-sizes="[10,20,30,50,100,200]"
    :page-size="pageSize"
     :layout="layout"
      :total="total"
      @size-change='getSize'
      @current-change='getChange'
    >
    </el-pagination>
    </div>
</template>

<script>
export default {
  name: 'cmsPagination',
  props: {
    align: {
      type: String,
      default: 'right'
    },
    layout: {
      type: String,
      default: 'total, sizes, prev, pager, next'
    },
    total: {
      type: Number,
      default: 0
    },
    size: {
      type: Number,
      default: 10
    },
    pSize: {
      type: Number,
      default: 10
    }
  },
  data() {
    return {
      current: 1,
      pageSize: 10
    }
  },
  watch: {
    pSize: {
      handler() {
        this.pageSize = this.pSize
      },
      immediate: true// 激活后立即触发一次
    }
  },
  methods: {
    getSize(val) { // 每页条数size
      this.pageSize = val
      this.$emit('change', this.current, val)
    },
    getChange(val) { // 当前页
      this.current = val
      this.$emit('change', val, this.pageSize)
    }
  },
  created() {
    this.pageSize = this.size
  }
}

</script>

<style lang='scss' scoped>
  .cms-pagination{
   padding: 20px 0;
   overflow: hidden;
  }
  
</style>



