<template>
  <!-- 导航树 -->
<el-dialog :visible="visible" title="选择文件夹" :append-to-body="true" width="300px" :before-close="handleClose">
  <div class="cms-zone-tree">
    <div class="tree-body" v-loading="loading">
      <ul id="fileTreeDialog"  class="ztree"  ref="treeDemo"></ul>
    </div>
  </div>
  <div slot="footer">
    <el-button type="primary" size="mini" @click="moveImg">移动</el-button>
     <el-button type="info" size="mini" @click="handleClose">取消</el-button>
  </div>
  </el-dialog>
</template>
<script>
import './../../../../static/js/jquery.ztree.all.min'
import {
  resourceSpaceTree,
  resourceDataShift
} from './../../../api/uploadZoom/upload.js'
export default {
  data() {
    return {
      visible: false,
      ownerType: 1, // 1是平台 2是店铺
      treeObj: null,
      setting: {
        callback: {
          onClick: this.getCheckNode
        },
        data: {
          key: {
            children: 'childs'
          }
        }
      },
      checkNodes: {}, // 当前点击的文件夹
      zNodes: [],
      ImgId: '',
      isLoad: false,
      loading: false
    }
  },
  methods: {
    initTreeFile(imgId) {
      // 创建图片空间文件树
      this.visible = true
      this.imgId = imgId

      this.loading = true
      resourceSpaceTree().then(res => {
        if (res.code === 200) {
          this.isLoad = true
          const root = [
            {
              name: '全部图片',
              open: true,
              id: 0,
              childs: res.data
            }
          ]
          this.zNodes = root
          this.$nextTick(() => {
            this.treeObj = $.fn.zTree.init(
              $('#fileTreeDialog'),
              this.setting,
              this.zNodes
            )
            this.loading = false
          })
        }
      })
    },
    getCheckNode(e, treeId, treeNode) {
      // 获取到当前点击的文件夹
      this.checkNodes = treeNode // 保存当前点击的节点
    },
    moveImg() {
      this.loading = true
      resourceDataShift({ id: this.imgId, space: { id: this.checkNodes.id }}).then(res => {
        if (res.code === 200) {
          this.visible = false
          this.imgId = ''
          this.checkNodes = {}
          this.$emit('change', true)
        } else {
          this.$errorMessage(res.code)
        }
        this.loading = false
      })
    },
    handleClose() {
      this.visible = false
    }
  }
}
</script>

<style scoped lang='scss'>
.cms-zone-tree {
  width: 300px;
 
}
.tree-body {
  height: 400px;
  overflow: auto;
  padding: 20px;
}
.tree-header {
  padding: 10px 16px;
  border-bottom: 1px solid #f0f0f0;
  overflow: hidden;
  display: flex;
  justify-content: space-between;
  align-items: center;
  .pic-dir {
    float: left;
    font-size: 12px;
    i {
      font-size: 20px;
      color: #00da38;
      position: relative;
      top: 3px;
    }
  }
  .new-fold {
    float: right;
    font-size: 12px;
    cursor: pointer;
    i {
      font-size: 20px;
      position: relative;
      top: 3px;
    }
  }
}
.right-menu {
  position: fixed;
  width: 60px;
  background: #fff;
  overflow: hidden;
  border: 1px solid #e0e0e0;
  border-radius: 4px;
  li {
    cursor: pointer;
    height: 24px;
    line-height: 24px;
    text-align: center;
    font-size: 12px;
    border-bottom: 1px solid #e0e0e0;
    &:last-child {
      border-bottom: none;
    }
  }
}
.show {
  display: block;
}
.hide {
  display: none;
}
</style>



