<!--图片空间选择-->
<template>
  <div class="cms-picture-zone">
    <div class="cms-picture-zone__inner">
      <div class="cms-picture-zone__header">
        <span class="fl text-overflow" style="max-width:650px;">{{resource.name}}</span>
        <div class="fr cms-picture-zone__search">
          <label>关键字</label>
          <el-input style="width:160px" placeholder="输入名称" v-model="params.alias" @keyup.enter="searchItems"></el-input>
          <el-button type='primary' size="mini" @click.native.prevent="searchItems" style="margin: 0 10px 0 0;">搜索</el-button>
          <div @click="showMoreSearch" class="more">
            <span class="el-dropdown-link">高级搜索
              <i v-show="showMore" class="el-icon-caret-top el-icon--right"></i>
              <i v-show="!showMore" class="el-icon-caret-bottom el-icon--right"></i>
            </span>
          </div>
          <!-- 这里一定要放在后面，不然的话 按回车会触发上传-->
          <cms-upload :on-change='getUpload' class="upload-abs">
            <el-button icon='up' size='mini' type="primary">上传</el-button>
          </cms-upload>
        </div>
      </div>
      <div v-show="showMore" class="more-search">
        <section class="cms-list-query">
          <div class="query-group">
            <label>资源别名</label>
            <el-input v-model="params.alias" placeholder="请输入资源别名"></el-input>
          </div>
          <div class="query-group">
            <label>资源大小</label>
              <el-input class="t-input" v-model="params.minSize" placeholder="最小值(KB)"></el-input>
              ~
              <el-input class="t-input" v-model="params.maxSize" placeholder="最大值(KB)"></el-input>
          </div>
          <div class="query-group" v-if="!type&&type === ''">
            <label>资源类型</label>
            <el-select v-model="params.resourceType" placeholder="请选择">
              <el-option v-for="(resource,index3) in types" :key="index3" :label="resource" :value="index3+1"></el-option>
            </el-select>
          </div>
        </section>
      </div>
      <div class="cms-picture-zone__batch">
        <div class="batch--fl">
          <el-checkbox v-model="checkAll" @change='getCheckAll'>全选</el-checkbox>
          <span @click="deletePictureItems">
            <i class="iconfont icon-shanchu1"></i> 批量删除</span>
        </div>
        <div class="batch--fr ">
          <label>排序</label>
          <el-select v-model="params.count" style="width:160px;margin-right:20px;" @change="itemsSort">
            <el-option v-for="(item,index) in timerItems" :key="index" :value="item.id" :label="item.name"></el-option>
          </el-select>
        </div>
      </div>
      <!--图片列表-->
      <div class="cms-picture-zone__body" ref="imgWarpper" id="imgWarpper">
        <ul class="zone--items--flex" ref="picItems" v-if="pictureItems.length>0">
          <li class="zone--items" :class="[isChecked(item) ? 'checked' : '']" v-for="(item,index) in pictureItems" :key="index" @contextmenu.prevent="openMenu(item,index,$event)" @click="setCheckedItems(item,index)">
            <div class="body--img-box">
              <img :src="item.url" alt="item.alias" :title="item.alias+'  '+item.dimensions">
            </div>
            <div class="body--img-name">
              <span v-show="!item.isEdit">{{item.alias}}</span>
              <input type="text" v-show="item.isEdit" v-model="item.alias" class="img-name-edit" @blur="savePictureName" :ref="'editInput'+index">
            </div>
            <!--上传遮罩-->
            <div class="upload-cover" v-if="item.isAdd">
              <el-progress type="circle" :percentage="item.percent" class="cover-progress" :status="item.status?'success':'exception'"></el-progress>
            </div>
          </li>
        </ul>
        <!-- 无内容显示 -->
        <div class="no-pictuer" v-else>
          <div>暂无内容，点击
            <cms-upload :on-change='getUpload'>
              <span class="upload-font">上传</span>
            </cms-upload>添加资源</div>
        </div>
        <!-- 右键 -->
        <ul class="zone-contextmenu" v-show="contextmenuVisible" :style="{left:left+'px',top:top+'px'}">
          <li @click="movePictureItem">
            <i class="iconfont icon-yidong"></i>
            移动
          </li>
          <li @click="editName">
            <i class="iconfont icon-jiaoxueneirong_zhongmingming"></i>
            重命名</li>
          <li @click="deletePictureItem">
            <i class="iconfont icon-shanchu"></i>
            删除
          </li>
        </ul>
      </div>
    </div>
    <div class="el-dialog__footer footer-flex">
      <el-button @click="setChange"  size='mini' type="primary" :disabled='disable' class="abs-button">确定</el-button>
      <cms-pagination @change="getPage" :size='20' :pSize="20"
       :total='total' align='left' layout='prev, pager, next' class="zone-pagination"></cms-pagination>
    </div>
    <!-- 文件树 -->
    <file-tree ref="fileTree" v-if="moveStatus" @change="changeMove"></file-tree>
  </div>
</template>
<script>
import {
  resourceDataPage,
  platformUpload,
  resourceDataDeletes,
  resourceDataUpdateName
} from '../../../api/uploadZoom/upload'
import fileTree from './fileTreeDialog'
export default {
  props: {
    size: [Number], // 控制选中的数量
    type: [String], // 当前资源类型
    resource: {
      // 资源文件夹id
      default: function() {
        return {}
      },
      type: [Object]
    }
  },
  components: {
    fileTree
  },
  data() {
    return {
      params: {
        count: 1,
        page: 1,
        size: 20,
        id: null,
        storeId: '',
        ownerType: 1,
        page_sort: 'createTime',
        page_sort_dir: 'desc',
        alias: '',
        maxSize: '',
        minSize: '',
        resourceType: 1,
        showType: 'list' // list or media
      },
      types: ['图片', '视频', '音频', 'word', 'excel', 'ppt', '压缩包', '其他'], // 资源分类
      showMore: false, // 是否显示更多搜索
      moveStatus: false,
      clickFlag: null, // 是否点击标识（定时器编号）
      total: 0, // 图片总数
      baseUrl: process.env.baseUrl, // 相对路径拼接
      pictureItems: [],
      disable: true,
      checkAll: false,
      checkedItems: [], // 选中图片的数组
      selectedItem: {}, // 当前操作的图片对象
      tmpName: '', // 临时存储修改名称
      selectedIndex: [], // 当前选中的是第几项
      contextmenuVisible: false,
      top: 0,
      left: 0,
      timerItems: [
        {
          name: '时间倒序',
          id: 1
        },
        {
          name: '时间顺序',
          id: 2
        },
        {
          name: '资源从小到大',
          id: 3
        },
        {
          name: '资源从大到小',
          id: 4
        }
      ]
    }
  },
  methods: {
    showMoreSearch() { // 显示更多搜索框
      this.showMore = !this.showMore
    },
    searchItems() {
      this.params.page = 1
      var reg = /^[0-9]*$/
      if (!reg.test(this.params.maxSize) || !reg.test(this.params.minSize)) {
        this.$errorMessage('请输入正确的数值')
        return
      }
      var min = parseInt(this.params.maxSize)
      var max = parseInt(this.params.minSize)
      if (min !== '' && max !== '') {
        if (max > min) {
          this.params.minSize = min
          this.params.maxSize = max
        }
      }
      this.getPictureItems()
    },
    itemsSort(value) {
      // 排序搜索
      if (value === 1) {
        this.params.page_sort = 'createTime'
        this.params.page_sort_dir = 'desc'
      }
      if (value === 2) {
        this.params.page_sort = 'createTime'
        this.params.page_sort_dir = 'asc'
      }
      if (value === 3) {
        this.params.page_sort = 'size'
        this.params.page_sort_dir = 'asc'
      }
      if (value === 4) {
        this.params.page_sort = 'size'
        this.params.page_sort_dir = 'desc'
      }
      this.getPictureItems()
    },
    validateType(file) {
      // 图片类型判断

      const type = file.type
      if (
        type === 'image/png' ||
        type === 'image/tiff' ||
        type === 'image/jpeg' ||
        type === 'image/jp2' ||
        type === 'image/gif' ||
        type === 'image/vnd.dwg' ||
        type === 'image/vnd.dxf'
      ) {
        return window.URL.createObjectURL(file)
      } else {
        return require('./../../../assets/images/type/other.png')
      }
    },
    getUpload(fileList) {
      // 获取到上传的文件
      this.$nextTick(() => {
        this.$refs.imgWarpper.scrollTop = 0
      }) // 滚动的时候回到最上面去
      for (const key in fileList) {
        if (fileList.hasOwnProperty(key)) {
          const obj = {
            url: this.validateType(fileList[key]),
            alias: fileList[key].name,
            id: '',
            isAdd: true,
            status: true,
            isEdit: false,
            percent: 0,
            dimensions: ''
          }
          this.checkAll = false
          this.pictureItems.unshift(obj)
          this.uploadFile(fileList[key], key)
        }
      }
    },
    uploadFile(file, index) {
      // file 上传的文件对象 index 批量上传确定是第几个 上传的自定义实现
      const param = new FormData() // 创建form对象
      param.append('uploadFile', file, file.name) // 通过append向form对象添加数据
      param.append('spaceId', this.resource.id) // 添加form表单中其他数据
      param.append('uploadPath', '') // 添加form表单中其他数据
      platformUpload(
        param,
        res1 => {
          // 处理上传进度的显示
          const loaded = res1.loaded
          const total = res1.total
          const percent = Math.round(loaded / total * 100)
          this.$set(this.pictureItems[index], 'percent', percent) // 给每一个上传项赋值,这里就会有上传的效果了
        },
        res2 => {
          if (res2.code === 200) {
            this.$set(this.pictureItems[index], 'id', res2.data.resourceId)
            this.$set(this.pictureItems[index], 'alias', res2.data.origName)
            this.$set(
              this.pictureItems[index],
              'dimensions',
              res2.data.dimensions
            )
            this.$set(this.pictureItems[index], 'status', true) // 判断进度成功
            setTimeout(() => {
              this.$set(this.pictureItems[index], 'isAdd', false) // 成功去掉遮罩
            }, 1000)
          } else {
            this.$set(this.pictureItems[index], 'status', false) // 判断进度失败
            this.$errorMessage(res2.message)
            setTimeout(() => {
              this.pictureItems.splice(index, 1)
            }, 1000)
          }
        }
      )
    },
    getPictureItems() {
      // 获取到点击的图片信息
      var types = ['', 'image', 'video', 'music', 'word', 'excel', 'ppt', 'zip', 'other']
      // 1:图片,2:视频,3:音频,4:word,5:excel,6:ppt,7:压缩包, 8:其他
      if (this.resource.id === 0) {
        this.params.id = ''
      } else {
        this.params.id = this.resource.id
      }
      this.params.resourceType = types.indexOf(this.type)
      resourceDataPage(this.params).then(res => {
        if (res.code === 200) {
          this.checkedItems = [] // 重置全选数据
          this.total = res.data.totalElements
          this.pictureItems = res.data.content
          this.pictureItems.forEach((element, index) => {
            // 获取图片接口信息,加一个修改标记
            this.$set(
              this.pictureItems[index],
              'url',
              this.baseUrl + element.url
            )
            this.$set(this.pictureItems[index], 'isEdit', false)
          })
        }
      })
    },
    getCheckAll(value) {
      // 全选操作
      if (value) {
        this.checkedItems = Object.assign([], this.pictureItems)
      } else {
        this.checkedItems = []
      }
    },
    isChecked(item) {
      // 判断是否选中
      return this.checkedItems.findIndex(v => v.id === item.id) > -1
    },
    setCheckedItems(item, index) {
      // 单击选中图片
      if (this.checkedItems.findIndex(v => v.id === item.id) === -1) {
        this.checkedItems.push(item)
      } else {
        this.checkedItems.splice(
          this.checkedItems.findIndex(v => v.id === item.id),
          1
        )
      }
    },
    editName() {
      // 重命名
      this.pictureItems[this.selectedIndex].isEdit = true
      const el = this.$refs['editInput' + this.selectedIndex]
      this.tmpName = this.selectedItem.alias
      setTimeout(() => {
        el[0].focus()
      }, 20)
    },
    savePictureName() {
      // 保存重命名
      const params = {
        id: this.selectedItem.id,
        alias: this.selectedItem.alias === '' ? this.tmpName : this.selectedItem.alias
      }
      if (this.selectedItem.alias === '' || this.selectedItem.alias === this.tmpName) {
        // 等于原来的就不要去掉接口了
        this.selectedItem.alias = this.tmpName
        this.pictureItems[this.selectedIndex].isEdit = false
      } else {
        resourceDataUpdateName(params).then(res => {
          if (res.code === 200) {
            setTimeout(() => {
              this.$successMessage('重命名成功')
            }, 50)
          } else {
            setTimeout(() => {
              this.$errorMessage(res.message)
            }, 50)
          }
        })
        this.pictureItems[this.selectedIndex].isEdit = false
      }
    },
    deletePictureItems() {
      // 批量删除图片项
      const ids = this.checkedItems.map(v => v.id)
      if (ids.length > 0) {
        resourceDataDeletes({ ids: ids }).then(res => {
          if (res.code === 200) {
            this.$successMessage('删除成功')
            this.getPictureItems()
          }
        })
      } else {
        this.$errorMessage('请至少选择一项进行删除操作')
      }
    },
    deletePictureItem() {
      // 删除图片项
      const id = this.selectedItem.id
      resourceDataDeletes({ ids: [id] }).then(res => {
        if (res.code === 200) {
          this.pictureItems.splice(this.selectedIndex, 1)
          this.$successMessage('删除成功')
        }
      })
    },
    movePictureItem() { // 移动图片
      this.moveStatus = true
      this.$nextTick(() => {
        this.$refs.fileTree.initTreeFile(this.selectedItem.id)
      })
    },
    changeMove(data) {
      this.getPictureItems()
    },
    setChange() {
      // todo,限制能勾选的文件类型
      if (this.checkedItems.length !== this.size) {
        this.$errorMessage(`最多只能选择${this.size}个文件`)
      } else {
        // 把选中的对象传递出来
        this.$emit('change', this.checkedItems)
        this.checkedItems = []
        this.selectedItem = {} // 当前操作的图片对象
        this.selectedIndex = [] // 当前选中的是第几项
      }
    },
    getPage(current, size) {
      // 分页调用
      this.params.page = current
      this.getPictureItems()
    },
    openMenu(item, index, e) {
      // 打开右键
      this.contextmenuVisible = true
      this.selectedItem = item
      this.selectedIndex = index
      this.left = e.clientX
      this.top = e.clientY
    },
    closeMenu() {
      // 关闭右键
      this.contextmenuVisible = false
    }
  },
  watch: {
    contextmenuVisible(value) {
      if (value) {
        window.addEventListener('click', this.closeMenu)
      } else {
        window.removeEventListener('click', this.closeMenu)
      }
    },
    checkedItems: {
      handler(curVal, oldVal) {
        const checkLen = this.checkedItems.length
        const itemLen = this.pictureItems.length
        this.checkAll = itemLen === checkLen && itemLen > 0 // 判断是否全选
        if (checkLen > 0) {
          this.disable = false
        } else {
          this.disable = true
        }
      },
      deep: true
    },
    resource: {
      handler() {
        this.params.page = 1
        this.params.alias = ''
        this.params.count = 1
        this.getPictureItems()
      },
      deep: true
    }
  },
  created() {
    this.getPictureItems()
  },
  mounted() {
    this.$nextTick(() => {
      // 滚动的时候 把当前显示的隐藏掉
      const box = document.getElementById('imgWarpper')
      box.onscroll = e => {
        if (e.target.scrollTop > 0) {
          this.closeMenu()
        }
      }
    })
  }
}
</script>

<style scoped lang='scss'>
  .upload-abs {
    position: absolute;
    left: 34px;
    top: 0;
  }
  .more{
    cursor: pointer;
    user-select: none;
  }
  .more-search{
    height: 55px;
  }
  .t-input{
    width: 96px !important;
  }
</style>



