
const product = {
  state: {
    cache: true,
    step: 'step2',
    categoryId: '', // 分类id
    storeId: 0, // 商家id
    productTypeId: 0, // 商品类型id
    categoryName: '',
    productInfo: {},
    updateProductObject: {}// 存储修改规格时候的参数信息
  },
  mutations: {

    SET_CACHE: (state, status) => {
      state.cache = status
    },
    SET_STEP: (state, step) => {
      state.step = step
    },
    SET_UPDATE_INFO: (state, info) => {
      state.updateProductObject = info
    },
    SET_PRODUCT_INFO: (state, info) => { // 修改商品时将数据回填保存
      state.productInfo = Object.assign({}, info)
    },
    SET_CATEGORY_INFO: (state, info) => { // 设置商品分类的信息
      let name = ''
      for (let i = 0; i < info.parentInfo.length; i++) {
        if (info.parentInfo[i].name !== '' && i === 0) {
          name = info.parentInfo[i].name
        } else if (info.parentInfo[i].name !== '') {
          name = name + ' > ' + info.parentInfo[i].name
        }
      }
      state.categoryName = name
      state.categoryId = info.categoryId
      state.step = 'step2'
    },
    SET_BASE_INFO: (state, baseInfo) => { // 设置商品基础信息
      const imageResources = baseInfo.imageResources
      const arr = []
      imageResources.forEach(v => { // 过滤掉没上传的图片项
        if (v.imgResourceId && v.imgResourceId !== '') {
          arr.push(v)
        }
      })
      baseInfo.imageResources = arr
      if (baseInfo.videoResources[0].vdResourceId === '') {
        baseInfo.videoResources = []
      }
      state.productInfo = Object.assign(state.productInfo, baseInfo)
      state.storeId = baseInfo.product.storeId
      state.productTypeId = baseInfo.productTypeId
    },
    SET_STANDARD_INFO: (state, standardInfo) => { // 设置规格属性信息
      state.productInfo = Object.assign(state.productInfo, standardInfo)
    },
    ClEAR_STATE: (state) => {
      state.categoryId = ''
      state.storeId = ''
      state.productTypeId = ''
      state.categoryName = ''
      state.productInfo = {}
      state.cache = false
    }
  },
  actions: {
    setProductInfo({ commit }, info) { // 将商品信息存进vuex
      commit('SET_PRODUCT_INFO', info)
    },
    setCache({ commit }, status) { // 判断是否缓存标识
      commit('SET_CACHE', status)
    },
    setStep({ commit }, step) {
      return new Promise(resolve => {
        commit('SET_STEP', step)
        resolve()
      })
    },
    setCateGoryInfo({ commit }, info) { // 设置分类信息
      commit('SET_CATEGORY_INFO', info)
    },
    setBaseProductInfo({ commit }, baseInfo) { // 设置商品基础信信息
      commit('SET_BASE_INFO', baseInfo)
    },
    setStandardInfo({ commit }, standardInfo) { // 设置商品规格属性信息
      return new Promise(resolve => {
        commit('SET_STANDARD_INFO', standardInfo)
        resolve(product.state.productInfo)
      })
    },
    clearProductInfo({ commit }) {
      commit('ClEAR_STATE')
    }
  }

}

export default product



