import CryptoJS from 'crypto-js'
export default {
  encrypt: function(word, encryptKey = 'WfJTKO9S4eLkrPz2JKrAnzdb', iv = 'D076D35C') { // 加密函数
    var keyHex = CryptoJS.enc.Utf8.parse(encryptKey)
    var encrypted = CryptoJS.TripleDES.encrypt(word, keyHex, {
      iv: CryptoJS.enc.Utf8.parse(iv),
      mode: CryptoJS.mode.CBC,
      padding: CryptoJS.pad.Pkcs7 })
    return encrypted.toString()
  },
  decrypt: function(word, encryptKey = 'WfJTKO9S4eLkrPz2JKrAnzdb', iv = 'D076D35C') {
    var keyHex = CryptoJS.enc.Utf8.parse(encryptKey)
    var encrypted = CryptoJS.TripleDES.decrypt(word, keyHex, {
      iv: CryptoJS.enc.Utf8.parse(iv),
      mode: CryptoJS.mode.CBC,
      // mode: CryptoJS.mode.ECB,
      padding: CryptoJS.pad.Pkcs7 })
    return encrypted.toString(CryptoJS.enc.Utf8)
  },
  // 过滤一下图片参数
  float: function(s, n = 2) {
    n = n > 5 ? 2 : n
    s = parseFloat((s + '').replace(/[^\d\.-]/g, '')) + ''
    const l = s.split('.')[0].split('').reverse()
    let r = s.split('.')[1]
    var t = ''
    for (var i = 0; i < l.length; i++) {
      t += l[i] + ((i + 1) % 3 === 0 && (i + 1) !== l.length ? ',' : '')
    }
    if (!r) {
      r = '0'
    }
    if (r.length < n) {
      for (let i = r.length; i < n; i++) {
        r += '0'
      }
    } else {
      r = r.substr(0, n)
    }
    if (n === 0) {
      return t.split('').reverse().join('')
    } else {
      return t.split('').reverse().join('') + '.' + r
    }
  },
  filterUrl: function(url) {
    if (url) {
      if (url.indexOf('http') > -1) {
        return url
      } else {
        return process.env.baseUrl + url
      }
    } else {
      return ''
    }
  },
  weChatUrl: function(url) {
    const base = process.env.baseUrl
    return `${base}/common/loadingImage?imageUrl=${url}`
  },
  // 验证对象类型
  validateObjcet: function(obj) {
    const type = Object.prototype.toString.call(obj)
    if (type === '[object Object]') {
      return 'object'
    } else if (type === '[object Array]') {
      return 'array'
    } else if (type === '[object String]') {
      return 'string'
    } else if (type === '[object Number]') {
      return 'number'
    } else if (type === '[object Function]') {
      return 'function'
    } else if (type === '[object Date]') {
      return 'date'
    } else if (type === '[object Boolean]') {
      return 'boolean'
    } else if (type === '[object Null]') {
      return 'null'
    } else if (type === '[object Undefined]') {
      return 'undefined'
    }
  },
  deepCopy: function(obj) {
    return JSON.parse(JSON.stringify(obj))
  },
  arrayToNumber: function(arr) { // 字符串转数组
    const tmp = []
    arr = arr.split(',')
    arr = arr.map((value) => {
      if (value !== '') {
        tmp.push(parseInt(value))
      }
    })

    return tmp
  },
  percentData: function(value) {
    // 百分比转换
    return (value * 100) + '%'
  },
  randomString: function(L) {
    var s = ''
    var randomchar = function() {
      var n = Math.floor(Math.random() * 62)
      if (n < 10) return n // 1-10
      if (n < 36) return String.fromCharCode(n + 55) // A-Z
      return String.fromCharCode(n + 61) // a-z
    }
    while (s.length < L) s += randomchar()
    return s
  },
  getDateDiff: function(timespan, istimesTamp) { // 时间字符串
    var dateTime = new Date(timespan)
    var year = dateTime.getFullYear()
    var month = dateTime.getMonth() + 1
    var day = dateTime.getDate()
    var hour = dateTime.getHours()
    var minute = dateTime.getMinutes()
    // var second = dateTime.getSeconds()
    var now = new Date()
    var now_new = now.getTime() // typescript转换写法
    var milliseconds = 0
    var timeSpanStr
    milliseconds = now_new - timespan

    if (milliseconds <= 1000 * 60 * 1) {
      timeSpanStr = '刚刚'
    } else if (1000 * 60 * 1 < milliseconds && milliseconds <= 1000 * 60 * 60) {
      timeSpanStr = Math.round((milliseconds / (1000 * 60))) + '分钟前'
    } else if (1000 * 60 * 60 * 1 < milliseconds && milliseconds <= 1000 * 60 * 60 * 24) {
      timeSpanStr = Math.round(milliseconds / (1000 * 60 * 60)) + '小时前'
    } else if (1000 * 60 * 60 * 24 < milliseconds && milliseconds <= 1000 * 60 * 60 * 24 * 3) {
      timeSpanStr = Math.round(milliseconds / (1000 * 60 * 60 * 24)) + '天前'
    } else if (milliseconds > 1000 * 60 * 60 * 24 * 3 && year === now.getFullYear()) {
      timeSpanStr = year + '-' + month + '-' + day + ' ' + hour + ':' + minute
    } else {
      timeSpanStr = year + '-' + month + '-' + day + ' ' + hour + ':' + minute
    }
    return timeSpanStr
  }
}



