<template>
  <div id="app">
    <router-view/>
  </div>
</template>

<script>
export default {
  name: 'App',
  mounted() {
    const loading = document.querySelector('#loading')
    loading.style.display = 'none'
    setTimeout(() => {
      document.querySelector('body').removeChild(loading)
    }, 10)
  }
}
</script>

<style>

</style>



