<template>
  <div>
    <div  class="clearfix" v-if="!disabled" style="margin-bottom:10px;"> 
    <div @click="createDialog" :class="float?'fl':''">
    <slot>
      <el-button type="primary" size='mini' >上传图片</el-button>
    </slot>
    </div>
    </div>
    <!--图片略缩容器-->
    <div class="viwer-box" v-if="preview&&viewType=='image'">
      <div class="viwer-warpper rectangular" v-for="(item,index) in pictureItems" :key="index">
        <img :src="$cms.filterUrl(item.url)" class="thumb-img">
        <div class="thumb-cover">
          <i class="el-icon-zoom-in" @click.stop='showViewer(item)'></i>
          <i class="el-icon-delete" @click.stop="deleteItems(index)"  v-if="!disabled"></i>
        </div>
      </div>
    </div>
    <div class="viwer-box" v-if="preview&&viewType=='media'">
      <ul class="viwer-media" v-for="(item,index) in pictureItems" :key="index">
        <li class="viwer-media-items">
          <div class="fl">
            <span class="viwer-media-icons">
              <i class="iconfont icon-shipin"></i>
            </span>
            <span class="viwer-media-name" >{{item.alias||'视频名称'}}</span>
          </div>
          <div class="fl">
            <span class="viwer-media-palyer" @click="mediaPlayer($cms.filterUrl(item.url))">播放</span>
            <span  class="viwer-media-delete" @click.stop="deleteItems(index)"  v-if="!disabled">删除</span>
          </div>
        </li>
      </ul>
    </div>
    <div  class="viwer-box" v-if="preview&&viewType=='music'">
    </div>
  </div>
</template>

<script>
import Vue from 'vue'
import zoneDialog from './zoneDialog'
var instance = null

export default {
  name: 'cmsPictureZone',
  props: {
    value: [String, Number],
    disabled: {// 禁用上传
      type: Boolean,
      default: false
    },
    preview: {
      default: true,
      type: Boolean
    }, // 自定义显示就把这个给false
    index: {
      // 自定义索引
      default: 0,
      type: Number
    },
    float: {
      default: true,
      type: Boolean
    },
    size: {
      // 最多能选择多少张，默认都是一次选一张
      default: 1,
      type: Number
    },
    type: {
      // 可选择资源的类型
      default: 'image', // image 图片，music 音乐，video 视频
      type: String
    },
    viewType: {
      default: 'image', // image，media多媒体,music  可选值
      type: String
    },
    params: {
      default: function() {
        return {}
      },
      type: Object
    },
    data: {
      // 数据结构参考资源的数据结构
      type: [Array, Object]
    },
    props: {// 默认的属性名，如果不同穿这个
      default: function() {
        return {
          id: 'id',
          url: 'url'
        }
      },
      type: Object
    }
  },
  data() {
    return {
      state: false, // 显示隐藏,
      resource: {
        // 资源文件对象
        name: '全部图片',
        id: ''
      },
      pictureItems: []
    }
  },
  methods: {
    createDialog() {
      // 实例化弹窗，避免多个弹出
      const $zoneEl = document.querySelector('#zoneDialog')
      if ($zoneEl) {
        instance.size = this.size
        instance.type = this.type
        instance.state = true
      } else {
        const ZoneDialog = Vue.extend(zoneDialog)
        instance = new ZoneDialog({})
        instance.size = this.size
        instance.type = this.type
        instance.$mount()
        document.body.appendChild(instance.$el)
      }
      instance.$on('change', items => {
        this.$emit('change', items, this.index, this.params)
        this.$emit('input', items[0].id)
        this.pictureItems = items
      })
    },
    mediaPlayer(url) {
      var w = window.open()
      setTimeout(() => {
        w.location = url
      }, 1)
      return false
    },
    showViewer(item) {
      // 查看预览
      this.$viewer({
        name: item.alias,
        url: item.url
      })
    },
    deleteItems(index) {
      // 删除对应的数据项
      this.pictureItems.splice(index, 1)
      if (this.pictureItems.length > 0) {
        this.$emit('change', this.pictureItems, this.index, this.params)
        this.$emit('input', this.pictureItems[0].id)
      } else {
        this.$emit('change', null, this.index, this.params)
        this.$emit('input', '')
      }
    }
  },
  watch: {
    data: {
      handler() {
        if (this.$cms.validateObjcet(this.data) === 'object') {
          this.pictureItems = []
          if (this.data[this.props.id] && this.data[this.props.id] !== '') {
            this.pictureItems.push(this.data)
          }
        } else {
          this.pictureItems = this.data
        }
      },
      deep: true,
      immediate: true
    }
  }
}
</script>

<style scoped lang='scss'>
.cms-zone-warper {
  overflow: hidden;
}
.viwer-box {
  overflow: hidden;
  max-width: 750px;
}
.viwer-warpper {
  float: left;
  position: relative;
  overflow: hidden;
  display: flex;
  align-items: center;
  justify-content: center;
 
  margin-right: 10px;
  width: 90px;
  height: 90px;
  border: 1px solid #e7e7e7;
  background: rgba(0, 0, 0, 0.7);
  cursor: pointer;
  &:hover {
    .thumb-cover {
      display: flex;
    }
  }
}
.square {
  width: 90px;
  height: 90px;
}
.rectangular {
  width: 140px;
  height: 60px;
}
.thumb-img {
  display: block;
  width: auto;
  height: auto;
  max-width: 100%;
  max-height: 100%;
}
.thumb-cover {
  display: none;
  position: absolute;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  top: 0;
  left: 0;
  text-align: center;
  align-items: center;
  justify-content: center;
  i {
    display: block;
    width: 35px;
    cursor: pointer;
    font-size: 22px;
    color: #fff;
  }
}
.viwer-media {
  overflow: hidden;
  &-items {
    overflow: hidden;
    margin-top: 10px;
     height: 32px;
    line-height: 32px;
  }
  &-icons {
    box-sizing: border-box;
      float: left;
    width: 32px;
     height: 32px;
    text-align: center;
    border: 1px solid #e7e7e7;
    margin-right: 10px;
    .iconfont {
      color: #369ef6;
      font-size: 28px;
    }
  }
  &-name {
    float: left;
    font-size: 12px;
    color: #444;
    width: 200px;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
  }
  &-palyer{
    color: #188ae2;
    cursor: pointer;
    margin-right: 16px;
  }
  &-delete{
      cursor: pointer;
    color: #999;
    &:hover{
      color: #ee3030;
    }
  }
}
</style>



