import router from './router'
import { getPrems } from './api/login/login'
import NProgress from 'nprogress' // progress bar
import 'nprogress/nprogress.css' // progress bar style
import store from './store/index'
NProgress.configure({
  showSpinner: false
})

// 控制权限用的代码
router.beforeEach((to, from, next) => {
  const token = localStorage.getItem('platKey') // 登录标示
  const perms = store.state.permissions.loginState
  if (token == null && to.path !== '/login') {
    NProgress.start()
    next('/login')
    next()
    NProgress.done()
  } else {
    if (to.path === '/login') {
      NProgress.start()
      next()
      NProgress.done()
    } else {
      if (perms) {
        NProgress.start()
        next()
        NProgress.done()
      } else {
        if (token !== '') {
          getPrems().then(res => {
            if (res.code === 200) {
              if (!res.data) { // 如果为空则判断系统异常，直接退出到登录
                next('/login')
              } else {
                store.dispatch('setUserInfo', {
                  userName: res.data.userName,
                  lastLoginTime: res.data.lastLoginTime,
                  lastLoginIP: res.data.lastLoginIP
                })
                store.dispatch('setRouter', res.data).then(route => {
                  router.addRoutes(route)
                  next({ ...to }) // hack方法 确保addRoutes已完成
                  store.dispatch('setDictionaryData')// 预加载字典信息
                  store.dispatch('setAreaData')// 预加载区域信息
                })
              }
            }
          })
        } else {
          next('/login')
        }
      }
    }
  }
})




