import cmsMessage from '@/components/cms/cmsMessage'
import cmsViewer from '@/components/cms/cmsViewer'
import router from '@/router/index'
import cms from './index'

import rules from '@/rules'

var moment = require('moment')
export default {
  install(Vue, options) {
    Vue.prototype.$cms = cms // 工具方法
    Vue.prototype.$routerLink = function(path, type, queryParams, moduleId = 0) {
      var params = {
        type: type
      }
      params = Object.assign(params, queryParams)
      // 路由
      router.push({ path: path, query: params })
    }

    Vue.prototype.$cmsMessage = cmsMessage // cms 提示框
    Vue.prototype.$rules = rules // 验证规则
    Vue.prototype.$moment = function(str, type = 'YYYY-MM-DD HH:mm:ss') {
      if (str) {
        return moment(parseInt(str)).format(type)
      } else {
        return '-'
      }
    }

    // 时间

    Vue.prototype.$successMessage = function(mes) {
      // 成功提示
      cmsMessage({
        message: mes,
        type: 'success',
        timer: '1000'
      })
    }
    Vue.prototype.$getUrl = function() {
      let url = location.href
      const http = url.substring(0, url.indexOf('//') + 2)
      url = url.substring(url.indexOf('//') + 2, url.length)
      if (url.indexOf('/') >= 0) {
        url = url.substring(0, url.indexOf('/'))
      }
      return http + url
    }
    Vue.prototype.$path = function(url) {
      if (url.indexOf('http') > -1) {
        return url
      } else {
        return process.env.baseUrl
      }
    }
    Vue.prototype.$viewer = function(options) {
      cmsViewer(options)
    }
    Vue.prototype.$errorMessage = function(mes) {
      // 成功提示
      cmsMessage({
        message: mes,
        type: 'error',
        timer: '1500'
      })
    }
    Vue.prototype.$getDictData = (type) => {
      // 获取对应的字典数据
      const data = localStorage.getItem('dictdata') ? localStorage.getItem('dictdata') : []
      const dictData = JSON.parse(data)
      if (dictData.length === 0) {
        return []
      } else {
        const index = dictData.findIndex(v => v.dictType === type)
        return dictData[index].datas
      }
    }
    Vue.prototype.$validateNum = function(el) {
      // 数字验证
      const val = el.target.value
      const reg = /^[1-9][0-9]{0,1}$/
      if (val === '') {
        el.target.className = 'cms-error el-input__inner'
        cmsMessage({
          message: '请输入一个有效数字',
          type: 'success',
          timer: '1000',
          fastClick: false
        })
      } else {
        if (reg.test(val)) {
          el.target.className = 'el-input__inner'
        } else {
          el.target.className = 'cms-error el-input__inner'
          cmsMessage({
            message: '只能输入1-99的正整数',
            type: 'success',
            timer: '1000',
            fastClick: false
          })
        }
      }
    }
  }
}



