<template>
  <div class="cms-breadcrumb clearfix">
    <slot name="body"></slot> 
    <ul class="cms-breadcrumb-items">
       <slot><li></li></slot> 
    </ul>
    <div class="cms-breadcrumb-back" > 
      <i class="iconfont icon-guanbi" @click="handleClick"></i>
        <transition name="cms-breadcrumb">
            <div class="cms-breadcrumb-tooltip" v-show="visble">
                点击此处返回{{title}}
              </div>  
        </transition>
      </div> 
  </div>
</template>

<script>
export default {
  name: 'cmsBreadcrumb',
  props: {
    to: {
      type: String,
      default: '/index'
    },
    type: {
      type: String,
      default: ''
    },
    params: {
      type: Object,
      default: function() {
        return {}
      }
    },
    title: {
      type: String,
      default: ''
    }
  },
  data() {
    return {
      visble: false
    }
  },
  methods: {
    handleClick(evt) {
      this.$emit('click', evt)
      setTimeout(() => {
        this.$routerLink(this.to, this.type, this.params)
      }, 0)
    }
  },
  created() {
    setTimeout(() => {
      this.visble = true
    }, 300)
    setTimeout(() => {
      this.visble = false
    }, 2000)
  }
}
</script>



