import { dictTypeManager } from './../../api/global'
import { areaTreeArea } from '@/api/setting/area.js'
const dict = {
  state: {
    data: [],
    area: []
  },
  mutations: {
    SET_DICT_DATA: (state, data) => {
      localStorage.setItem('dictdata', JSON.stringify(data))
      state.data = data
    },
    SET_AREA_DATA: (state, data) => {
      state.area = data
    },
    SELECT_DCIT_DATA: (state, type) => {
      return state.data.filter(v => v.dictType === type)
    }
  },
  actions: {
    setDictionaryData({ commit }) {
      dictTypeManager().then(res => { // 后台获取到字典数据保存起来，避免每次请求
        if (res.code === 200) {
          commit('SET_DICT_DATA', res.data)
        }
      })
    },
    setAreaData({ commit }) {
      areaTreeArea().then(res => { // 后台获取到字典数据保存起来，避免每次请求
        if (res.code === 200) {
          commit('SET_AREA_DATA', res.data)
        }
      })
    }

  }
}
export default dict




