<template>
  <!-- 导航树 -->
  <div class="cms-zone-tree">
    <div class="tree-header">
      <span class="pic-dir">
        <i class="iconfont icon-tupian3"></i> 图片目录</span>
      <span class="new-fold" @click="addNodes">
        <i class="iconfont icon-xinjianwenjianjia"></i> 新建文件夹</span>
    </div>
    <div class="tree-body">
      <ul id="treeDemo" class="ztree" ref="treeDemo"></ul>
    </div>
    <ul class="right-menu" ref="rightMenu" v-show="contextmenuVisible" :class="[contextmenuVisible?'show':'hide']" :style="position">
      <li @click='realName'>重命名</li>
      <li @click="deleteNode">删除</li>
    </ul>
  </div>
</template>
<script>
 import './../../../../static/js/jquery.ztree.all.min'
import {
   resourceSpaceTree,
   resourceSavePlatform,
   resourceUpdatePlatform,
   resourceSaveDelete
} from './../../../api/uploadZoom/upload.js'
export default {
   data() {
     return {
       contextmenuVisible: false,
       position: {
         left: 0,
         top: 0
       },
       ownerType: 1, // 1是平台 2是店铺
       treeObj: null,
       setting: {
         callback: {
           onClick: this.getCheckNode,
           onRename: this.editNodeName,
           beforeEditName: this.editName,
           onRightClick: this.onRightClick
         },
         data: {
           key: {
             children: 'childs'
           }
         }
       },
       checkNodes: {}, // 当前点击的文件夹
       zNodes: []
     }
   },
   methods: {
     onRightClick(event, treeId, treeNode) {
       this.checkNodes = treeNode
       if (treeNode.id !== 0) {
         this.contextmenuVisible = true
         this.position.left = event.clientX + 15 + 'px'
         this.position.top = event.clientY + 15 + 'px'
       }
     },
     ceatedTreeNode() {
       // 创建图片空间文件树
       resourceSpaceTree({ ownerType: this.ownerType }).then(res => {
         if (res.code === 200) {
           const root = [
             {
               name: '全部图片',
               open: true,
               id: 0,
               childs: res.data
             }
           ]
           this.zNodes = root
           this.$nextTick(() => {
             this.treeObj = $.fn.zTree.init(
               $('#treeDemo'),
               this.setting,
               this.zNodes
             )
           })
         }
       })
     },
     getCheckNode(e, treeId, treeNode) {
       // 获取到当前点击的文件夹
       this.checkNodes = treeNode // 保存当前点击的节点
       this.$emit('get', {
         name: treeNode.name,
         id: treeNode.id
       })
     },
     deleteNode() { // 删除文件夹
       this.$confirm('此操作将永久删除该文件夹, 是否继续?', '提示', {
         confirmButtonText: '确定',
         cancelButtonText: '取消',
         type: 'warning'
       }).then(() => {
         resourceSaveDelete({ ids: [this.checkNodes.id] }).then(res => {
           if (res.code === 200) {
             this.treeObj.removeNode(this.checkNodes)
             document.querySelector('#treeDemo_1_span').click()
           }
         })
       }).catch(() => {})
     },
     realName() {
       this.treeObj.editName(this.checkNodes)
     },
     addNodes() {
       // 添加文件夹节点
       let returnObj
       const newNode = {
         name: '新建文件夹',
         parentId: '',
         isAdd: true
       }
       if (this.checkNodes.id && this.checkNodes.id !== 0) {
         newNode.parentId = this.checkNodes.id
         returnObj = this.treeObj.addNodes(this.checkNodes, newNode)
       } else {
         newNode.parentId = ''
         const node = this.treeObj.getNodeByParam('id', 0)
         returnObj = this.treeObj.addNodes(node, newNode)
       }
       this.treeObj.editName(returnObj[0])
     },
     editNodeName(e, treeId, treeNode, isCancel) { // 修改添加文件夹名称
       if (treeNode.name === '') {
         this.ceatedTreeNode()
       } else {
         if (treeNode.name.length > 150) {
           treeNode.name = treeNode.name.substring(0, 150)
         }
         if (treeNode.isAdd) { // 新建文件
           resourceSavePlatform({
             name: treeNode.name,
             parentId: treeNode.parentId,
             sortNum: 10
           }).then(res => {
             if (res.code === 200) {
               treeNode.id = res.data.id
               this.$successMessage('新增成功')
             } else {
               this.treeObj.removeNode(treeNode)
             }
           })
         } else { // 修改文件名
           resourceUpdatePlatform({
             id: treeNode.id,
             name: treeNode.name
           })
             .then(res => {
               if (res.code === 200) {
                 setTimeout(() => {
                   this.$successMessage('修改成功')
                 }, 50)
               }
             })
         }
       }
     },
     closeMenu() {
       this.contextmenuVisible = false
     }
   },
   created() {
     this.ceatedTreeNode()
   },
   watch: {
     contextmenuVisible(value) {
       if (value) {
         window.addEventListener('click', this.closeMenu)
       } else {
         window.removeEventListener('click', this.closeMenu)
       }
     }
   }
}
</script>

<style scoped lang='scss'>
.cms-zone-tree {
  width: 220px;
  border-right: 1px solid #f0f0f0;
  float: left;
}
.tree-body {
  height: 560px;
  overflow: auto;
}
.tree-header {
  padding: 10px 16px;
  border-bottom: 1px solid #f0f0f0;
  overflow: hidden;
  display: flex;
  justify-content: space-between;
  align-items: center;
  .pic-dir {
    float: left;
    font-size: 12px;
    i {
      font-size: 20px;
      color: #00da38;
      position: relative;
      top: 3px;
    }
  }
  .new-fold {
    float: right;
    font-size: 12px;
    cursor: pointer;
    i {
      font-size: 20px;
      position: relative;
      top: 3px;
    }
  }
}
.right-menu {
  position: fixed;
  width: 60px;
  background: #fff;
  overflow: hidden;
  border: 1px solid #e0e0e0;
  border-radius: 4px;
  li {
    cursor: pointer;
    height: 24px;
    line-height: 24px;
    text-align: center;
    font-size: 12px;
    border-bottom: 1px solid #e0e0e0;
    &:last-child {
      border-bottom: none;
    }
  }
}
.show {
  display: block;
}
.hide {
  display: none;
}
</style>



