import cmsModules from './../../router/routeModule'
import {
  indexComponent,
  notFound
} from '../../router/routeMap'

function filterRouter(routersMap) { // 递归路由信息
  var filterArray = []
  routersMap.map(v => {
    v.meta = {
      name: v.component
    }
    if (v.component) {
      v.component = cmsModules[v.component]
    }
    if (v.children && v.children.length > 0) {
      filterRouter(v.children)
    }
    filterArray.push(v)
  })
  return filterArray
}
const permissions = {
  state: {
    devStatus: true, // 开发模式,开启的时候就会忽略后台来的权限，得到
    loginState: false, // 登陆状态
    ansyRouterMap: [], // defaultRouter.concat(ansycMap), // 后端角色菜单，等所有功能完成以后把这里改掉
    prems: [] // 按钮的权限控制
  },
  mutations: {
    SET_ROUTERS: (state, prems) => {
      state.ansyRouterMap = prems.menus
      state.prems = prems.routings
      state.loginState = true
    }
  },
  actions: {
    setRouter({
      commit
    }, prems) { // prems 菜单和按钮的权限
      return new Promise(resolve => {
        const accessedRouters = indexComponent.concat(filterRouter(prems.menus), notFound)

        commit('SET_ROUTERS', {
          menus: accessedRouters,
          routings: prems.routings
        })
        resolve(accessedRouters)
      })
    }
  }

}

export default permissions



