// 商城系统设置相关
const localColor = localStorage.getItem('color') == null ? '#188ae2' : localStorage.getItem('color')// 本地存储上次的颜色

const system = {
  state: {
    color: localColor,
    globalData: {
      attrs: {}
    }, // 商城系统设置
    globalDataTag: 'first', // 系统设置选项卡切换

    city: []
  },
  mutations: {
    SET_COLOR: (state, color) => {
      if (color == null) {
        color = '#188ae2'
      }
      localStorage.setItem('color', color)
      state.color = color
    },
    SET_GLOBAL_DATA: (state, data) => {
      state.globalData = data
    },
    SET_GLOBAL_TAG: (state, activeName) => {
      state.globalDataTag = activeName
    }
  },
  actions: {
    setColor({ commit }, color) {
      return new Promise(resolve => {
        commit('SET_COLOR', color)
        resolve()
      })
    },
    setGlobalData({ commit }, data) {
      commit('SET_GLOBAL_DATA', data)
      return Promise.resolve(true)
    }
  }

}

export default system



