// 图片空间资源相关接口
import axios from './../fetch'
// 图片空间文件夹
export const resourceSpaceTree = params => { return axios.get('/admin/platform/resourcespace/findAll', { params: params }) }

// 图片空间文件夹新增
export const resourceSavePlatform = params => { return axios.post('/admin/platform/resourcespace/savePlatform', params) }
// 图片空间文件夹删除
export const resourceSaveDelete = params => { return axios.post('/admin/platform/resourcespace/delete', params) }
// 图片空间文件夹名称修改
export const resourceUpdatePlatform = params => { return axios.post('/admin/platform/resourcespace/update', params) }

// 图片资源列表 支持分页
export const resourceDataPage = params => { return axios.get('/admin/platform/resourcespaceData/page', { params: params }) }// 改成page
// 资源删除
export const resourceDataDeletes = params => { return axios.post('/admin/platform/resourcespaceData/deletes', params) }
// 资源重命名
export const resourceDataUpdateName = params => { return axios.post('/admin/platform/resourcespaceData/update', params) }
// 资源重命名
export const resourceDataShift = params => { return axios.post('/admin/platform/resourcespaceData/shift', params) }

// 平台上传
export const platformUpload = (params, callback1, callback2) => {
  return axios({
    url: '/admin/platform/upload/o_upload',
    headers: { 'Content-Type': 'multipart/form-data' },
    method: 'post',
    onUploadProgress: function(progressEvent) { // 原生获取上传进度的事件
      if (progressEvent.lengthComputable) {
        // 属性lengthComputable主要表明总共需要完成的工作量和已经完成的工作是否可以被测量
        // 如果lengthComputable为false，就获取不到progressEvent.total和progressEvent.loaded
        callback1(progressEvent)
      }
    },
    data: params
  }).then(res => {
    callback2(res)
  })
}

// 服务器图片同步到微信
export const synchServerImg = params => { return axios.get('/admin/platform/wechatMaterial/synchServerImg', { params: params }) }




