// 统计用相关数据
var moment = require('moment')
const statistics = {
  state: {
    panelDate: moment().subtract(1, 'days').format('YYYY-MM-DD')

  },
  mutations: {
    SET_PANEL_DATE: (state, date) => {
      state.panelDate = date
    }

  },
  actions: {
    setPanelDate({ commit }, date) {
      return new Promise(resolve => {
        commit('SET_PANEL_DATE', date)
        resolve()
      })
    }
  }

}

export default statistics



