import Vue from 'vue'
import Vuex from 'vuex'
import sideBar from './module/sideBar'// 侧边栏
import tagsView from './module/tagsView'// 选项卡
import system from './module/system'// 系统设置
import permissions from './module/premission'// 权限
import user from './module/user'// 用户
import dict from './module/dictionary'
import statistics from './module/statistics'
import product from './module/product'// 权限
import unreadInfo from './module/unreadInfo'
Vue.use(Vuex)

const store = new Vuex.Store({
  modules: {
    sideBar,
    tagsView,
    system,
    permissions,
    user,
    dict,
    product,
    statistics,
    unreadInfo
  }
})

export default store



