<template>
    <!-- <el-button type="primary" @click.native="state=true" size='mini'>{{label}}</el-button> -->
    <el-dialog title="资源空间" :visible.sync="state" width="1090px" top="10vh" :before-close="handleClose" id="zoneDialog">
      <div class="cms-zone-warper">
        <picture-tree @get='getResourcesId'></picture-tree>
        <picture-zone :resource='resource' @change="getCheckedItems" :type='type' :size='size'></picture-zone>
      </div>
    </el-dialog>
</template>
<script>
// 通过change事件触发可以得到图片的id和路径
import pictureTree from './fileTree.vue'
import pictureZone from './pictureZone.vue'
export default {
  components: {
    pictureTree,
    pictureZone
  },
  name: 'zoneDialog',
  props: {
    size: {// 最多能选择多少张，默认都是一次选一张
      default: 1,
      type: Number
    },
    type: {// 可选择的类型
      default: 'image',
      type: String
    }
  },
  data() {
    return {
      state: true, // 显示隐藏,
      resource: {// 资源文件对象
        name: '全部图片',
        id: ''
      },
      pictureItems: []
    }
  },
  methods: {
    showViewer(item) { // 查看预览
      this.$viewer({
        name: item.alias,
        url: item.url
      })
    },
    getResourcesId(obj) { // 通过树点击获取到资源文件夹的对象
      this.resource = obj
    },
    getCheckedItems(items) {
      // 获取到传递的图片对象
      this.pictureItems = items
      this.$emit('change', items)
      this.close()
    },
    close() { // 关闭弹窗
      this.$nextTick(() => {
        this.state = false
        setTimeout(() => {
          this.$destroy()
          const $zoneEl = document.querySelector('#zoneDialog')
          $zoneEl.parentNode.removeChild($zoneEl)
        }, 5)
      })
    },
    handleClose(done) { // 关闭弹窗
      done()
      this.close()
    }
  }
}
</script>

<style scoped lang='scss'>
.cms-zone-warper {
  overflow: hidden;
}

</style>



