// 设置-区域相关接口
import axios from './../fetch'
// 区域列表分页
export const areaRegionList = params => { return axios.get('/admin/platform/area/list', { params: params }) }

// 区域详情
export const areaRegionGet = params => { return axios.get('/admin/platform/area/get', { params: params }) }

// 根据区域查询area
export const areaTreeArea = params => { return axios.get('/admin/platform/area/tree', { params: params }) }

// 根据code查询是否唯一
export const areaRegionGetAreaCode = params => { return axios.get('/admin/platform/area/getAreaCode', { params: params }) }

// 区域新增
export const areaRegionSave = params => { return axios.post('/admin/platform/area/save', params) }

// 区域删除
export const areaRegionDelete = params => { return axios.post('/admin/platform/area/delete', params) }

// 区域修改
export const areaRegionUpdate = params => { return axios.post('/admin/platform/area/update', params) }

// 区域保存排序
export const areaRegionSort = params => { return axios.post('/admin/platform/area/sort', params) }



