// 品牌管理相关接口
import axios from '../fetch'

// 品牌列表分页
export const brandList = params => { return axios.get('/admin/platform/brand/page', { params: params }) }

// 修改品牌列表
export const brandUpdate = params => { return axios.post('/admin/platform/brand/update', params) }

// 批量删除品牌列表
export const brandDelete = params => { return axios.post('/admin/platform/brand/deleteIds', params) }

// 添加品牌列表
export const brandSave = params => { return axios.post('/admin/platform/brand/save', params) }

// 获取单个品牌
export const brandGet = params => { return axios.get('/admin/platform/brand/get', { params: params }) }

// 审核不通过
export const brandNot = params => { return axios.post('/admin/platform/brand/updateNotThrough', params) }

// 审核通过
export const brandAdopt = params => { return axios.post('/admin/platform/brand/updateAdopt', params) }

// 保存排序值
export const brandSort = params => { return axios.post('/admin/platform/brand/sort', params) }

// 获取所有品牌的信息 品牌选择器使用
export const brandListAll = params => { return axios.get('/admin/platform/brand/list', { params: params }) }



