<template>
    <div class="cms-viewer" @mousewheel="zoomPicture" v-show="show" id="cmsViewer">
      <div class="cms-viewer-header">
        <span>{{name}}</span>
        <i class="el-icon-close" @click="close" style="margin-left:15px;"></i>
      </div>
      <div class="cms-viewer-body">
        <img :src="$cms.filterUrl(url)" alt="" :style="{'transform':'scale('+scale+','+scale+')'}">
      </div>
      <div class="cms-viewer-footer">
        <i class="el-icon-arrow-left" v-if="list.length>0" @click="prev"></i>
        <i class="el-icon-zoom-out" @click="clickZoom(-0.1)"></i>
        <span>{{parseInt(scale*100)}}%</span>
        <i class="el-icon-zoom-in" @click="clickZoom(0.1)"></i>
        <i class="el-icon-arrow-right" v-if="list.length>0" @click="next"></i>
        <i class="el-icon-close" @click="close" style="margin-left:15px;"></i>
      </div>
    </div>
</template>
<script>
export default {
  data() {
    return {
      name: '',
      url: '',
      show: false,
      scale: 1,
      list: [],
      active: 0
    }
  },
  computed: {
    path() {
      return this.url
    }
  },
  methods: {
    prev() {
      if (this.active === 0) {
        return false
      } else {
        this.active -= 1
        this.changeImage(this.active)
      }
    },
    next() {
      if (this.active === (this.list.length - 1)) {
        return false
      } else {
        this.active += 1
        this.changeImage(this.active)
      }
    },
    changeImage(index) {
      this.url = this.list[index].url// 不一定叫url 最好动态化
      this.scale = 1
    },
    clickZoom(step) {
      this.scale += step
      if (this.scale <= 0.1) {
        this.scale = 0.1
      } else if (this.scale >= 3) {
        this.scale = 3
      }
    },
    zoomPicture(e) {
      const step = e.wheelDelta / 1200
      this.clickZoom(step)
    },
    close() {
      this.show = false
      document.removeEventListener('keyup', this.escClose)
    },
    escClose(e) {
      if (e.keyCode === 27) {
        this.close()
      }
    }
  },
  created() {
    if (this.list.length > 0) { // 这里要做好判断
      this.url = this.list[this.active].url// 不一定叫url 最好动态化
    }
  },
  mounted() {
    document.addEventListener('keyup', this.escClose)
  }
}
</script>

<style lang="scss" scoped>
.cms-viewer {
  position: fixed;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.8);
  z-index: 999999;
  top: 0;
  left: 0;
  // top: 50%;
  // left: 50%;
  // margin: -380px 0 0 -600px;
  overflow: hidden;
  &-header {
    height: 30px;
    line-height: 30px;
    width: 100%;
    font-size: 18px;
    z-index: 888;
    position: absolute;
    top:0;
    color: #fff;
    background: rgba(0, 0, 0, 0.3);
    text-align: center;
    > i {
      position: absolute;
      width: 30px;
      height: 30px;
      right: 0;
      top: 0;
      line-height: 30px;
      cursor: pointer;
    }
  }
  &-body {
    position:absolute; width:100%; top:30px;
     bottom:40px;
     overflow:auto;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    align-items: center;
    img {
      width: auto;
      height: auto;
      max-width: 100%;
      max-height: 100%;
      cursor: zoom-in;
    }
  }
  &-footer {
    width: 60%;
    height: 40px;
    background: rgba(0, 0, 0, 0.3);
    position: absolute;
    bottom: 0;
    margin-left: 20%;
    font-size: 20px;
    color: #fff;
    transform: scale(1, 1);
    display: flex;
    align-items: center;
    justify-content: center;
    > i {
      cursor: pointer;
    }
    > span {
      width: 60px;
      text-align: center;
      margin: 0 10px;
    }
  }
}
</style>






