import {
  storeMessageAllpage,
  storeMessageUnread
} from './../../api/index/index'

const unreadInfo = {
  state: {
    infoNum: 0,
    infoData: []
  },
  mutations: {
    SET_INfO_NUM: (state, obj) => {
      state.infoNum = obj.infoNum
    },
    SET_INfO_DATA: (state, obj) => {
      state.infoData = obj.infoData
    }
  },
  actions: {
    setInfoData({ commit }, obj) {
      return new Promise((resolve, reject) => {
        const params = {
          page: 1,
          size: 5
        }
        storeMessageUnread().then(res => {
          if (res.code === 200) {
            commit('SET_INfO_NUM', {
              infoNum: res.data.UnreadMessage
            })
          }
        })
        storeMessageAllpage(params).then(res => {
          resolve(res)
          if (res.code === 200) {
            const arr = []
            res.data.content.forEach(v => {
              if (v.status === 0) {
                arr.push(v)
              }
            })
            commit('SET_INfO_DATA', {
              infoData: arr
            })
          }
        })
      })
    }
  }
}
export default unreadInfo



