/**
 *
 * @param {String} path  路由地址
 * 地址要不弹出选项卡必须是父子关系
 */
function testRouter(path1) {
  const count = (path1.split('/')).length - 1

  if (count >= 2) {
    const targetStr = path1.substring(0, find(path1, '/', 1))
    return targetStr
  } else {
    return path1
  }
}

function find(str, cha, num) {
  var x = str.indexOf(cha)
  for (var i = 0; i < num; i++) {
    x = str.indexOf(cha, x + 1)
  }
  return x
}
const tagsView = {
  state: {
    indexViews: {
      name: '控制台',
      path: '/index',
      title: '控制台'
    },
    visitedViews: [{
      name: '控制台',
      path: '/index',
      title: '控制台'
    }],
    cachedViews: []
  },
  mutations: {
    ADD_VISITED_VIEWS: (state, view) => {
      let metaName = ''
      if (view.matched.length === 3) {
        metaName = view.meta.name
      } else if (view.matched.length === 4) {
        metaName = view.matched[view.matched.length - 1].parent.meta.name
      }
      const status = state.visitedViews.some(v => {
        return view.path.indexOf(v.path) > -1 || v.path.indexOf(testRouter(view.path)) > -1
      })
      if (status) {
        const index = state.visitedViews.findIndex(v => view.path.indexOf(v.path) > -1 || v.path.indexOf(testRouter(view.path)) > -1)
        state.visitedViews[index] = {
          name: view.name,
          path: view.fullPath,
          title: view.name || view.meta.title,
          meta: metaName
        }
      } else {
        state.visitedViews.push({
          name: view.name,
          path: view.fullPath,
          title: view.name || view.meta.title,
          meta: metaName
        })
      }
      for (const i of state.cachedViews) { // 去除重复的cache
        if (i === metaName) {
          const index = state.cachedViews.indexOf(i)
          state.cachedViews.splice(index, 1)
          break
        }
      }
      state.cachedViews.push(metaName)
    },
    DEL_VISITED_VIEWS: (state, view) => {
      for (const [i, v] of state.visitedViews.entries()) {
        if (v.path === view.path) {
          state.visitedViews.splice(i, 1)
          break
        }
      }

      for (const i of state.cachedViews) {
        if (i === view.meta) {
          const index = state.cachedViews.indexOf(i)
          state.cachedViews.splice(index, 1)
          break
        }
      }
    },
    DEL_OTHERS_VIEWS: (state, view) => {
      for (const [i, v] of state.visitedViews.entries()) {
        if (v.path === view.path) {
          state.visitedViews = state.visitedViews.slice(i, i + 1)
          state.visitedViews.unshift(state.indexViews)
          break
        }
      }
      for (const i of state.cachedViews) {
        if (i === view.name) {
          const index = state.cachedViews.indexOf(i)
          state.cachedViews = state.cachedViews.slice(index, i + 1)
          state.cachedViews.unshift(state.indexViews)
          break
        }
      }
    },
    DEL_ALL_VIEWS: (state) => {
      state.visitedViews = []
      state.cachedViews = []
    }
  },
  actions: {
    addVisitedViews({
      commit
    }, view) {
      commit('ADD_VISITED_VIEWS', view)
    },
    delVisitedViews({
      commit,
      state
    }, view) {
      return new Promise((resolve) => {
        commit('DEL_VISITED_VIEWS', view)
        resolve([...state.visitedViews])
      })
    },
    delOthersViews({
      commit,
      state
    }, view) {
      return new Promise((resolve) => {
        commit('DEL_OTHERS_VIEWS', view)
        resolve([...state.visitedViews])
      })
    },
    delAllViews({
      commit,
      state
    }) {
      return new Promise((resolve) => {
        commit('DEL_ALL_VIEWS')
        resolve([...state.visitedViews])
      })
    }
  }
}

export default tagsView



